import * as THREE from 'three';
import { useVideoTexture } from '@react-three/drei';
import { OrbitControls } from "@react-three/drei"; //Camera Controls
import { useFrame } from '@react-three/fiber';
import React, { Suspense, useState, useRef } from "react";

const VideoScene = ()=>
{
    //references
    const videoSphereRef = useRef();

    // const videoTexture = useVideoTexture("./videos/shorten360Vid.mp4");

    const [video] = useState(() => {
        const vid = document.createElement("video");
        vid.src = "./videos/street360.mp4";
        vid.crossOrigin = "Anonymous";
        vid.loop = true;
        vid.muted = true;
        vid.play();
        return vid;
      });

    useFrame((state, delta)=>{ //The Update function, runs each frame
        
    });

    return (
        <>
            <OrbitControls enableZoom={false} enablePan={false}></OrbitControls>
            {/* <axesHelper args={[1000]}></axesHelper> */}
            <Suspense fallback={
                <>
                    <mesh>
                        <boxGeometry></boxGeometry>
                        <meshBasicMaterial color="red"></meshBasicMaterial>
                    </mesh>
                </>
            }>
                <mesh scale={[-10,10,10]} ref={videoSphereRef}>
                    <sphereGeometry></sphereGeometry>
                    <meshBasicMaterial 
                        color="lightblue" 
                        side={THREE.DoubleSide} >
                            <videoTexture attach="map" args={[video]} />
                        </meshBasicMaterial>
                </mesh>
            </Suspense>
            
        </>
    )
}



export default VideoScene;